


















import Vue from 'vue';
import { Component, Prop } from 'vue-property-decorator';

@Component({
  name: 'search-bar'
})
export default class SearchBar extends Vue {
  @Prop() errorMessages: string;
  searchTerm: string;

  constructor() {
    super();
    this.searchTerm = null;
  }

  addCity() {
    this.$emit('addCity', this.searchTerm);
    this.searchTerm = null;
  }
}
